* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-size: inherit; }

html, body {
  margin: 0;
  padding: 0;
  font-family: Menlo, Consolas, monospace;
  font-size: large;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #FFFFFF; }

header {
  background-color: #F5F5F5;
  border-bottom: 1px solid #E0E0E0;
  line-height: 50px;
  font-size: larger; }

nav {
  background-color: #FAFAFA;
  border-bottom: 1px solid #E0E0E0; }

footer {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  padding: 10px 0;
  background-color: #FAFAFA;
  border-top: 1px solid #E0E0E0;
  text-align: center;
  font-size: medium;
  color: #888888; }

form {
  padding: 10px 50px; }

form > section {
    display: flex;
    margin: 20px 0; }

form > section > label {
      width: 200px; }

form > section > article {
      flex: 1; }

button, .button {
  padding: 6px 12px;
  border-radius: 4px;
  border: 1px solid #E0E0E0;
  background-color: white;
  cursor: pointer; }

button:hover, .button:hover {
    background-color: #F0F0F0; }

button[disabled], .button[disabled] {
    background-color: #FAFAFA;
    color: #AAAAAA; }

a {
  text-decoration: none; }

a:hover {
    text-decoration: underline; }

.content {
  margin: auto;
  width: 1000px; }

.border {
  border-bottom: 1px solid #E0E0E0; }

.white, .white0 {
  background-color: #FFFFFF; }

.white1 {
  background-color: #FAFAFA; }

.white2 {
  background-color: #F5F5F5; }

.white3 {
  background-color: #F0F0F0; }

small[data-v-765665de] {
  font-size: x-small;
}

select[data-v-77d1d7fc] {
  width: 300px;
}
select.error[data-v-77d1d7fc] {
    border-color: red;
}
.file[data-v-77d1d7fc] {
  display: flex;
}
.upload[data-v-77d1d7fc] {
  position: relative;
  width: 300px;
  height: 100px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.upload input[data-v-77d1d7fc] {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    cursor: pointer;
}
.info[data-v-77d1d7fc] {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  margin-left: 10px;
}
.info > div[data-v-77d1d7fc] {
    white-space: pre-wrap;
}

.output[data-v-6397a225] {
  padding: 0 50px 150px;
}

.message {
  color: #333333;
  margin: 30px 0;
}
.message article {
    padding: 10px;
    border: 1px solid #E0E0E0;
    background-color: #F5F5F5;
}
.message article p {
      white-space: pre-wrap;
      word-break: break-all;
}
.message .complete, .message .abort, .message .error {
    padding: 4px 10px;
    border-radius: 4px;
    border: 1px solid #E0E0E0;
    color: #FFFFFF;
}
.message .complete {
    background-color: #5cb85c;
    border-color: #4cae4c;
}
.message .abort {
    background-color: #f0ad4e;
    border-color: #eea236;
}
.message .error {
    background-color: #c9302c;
    border-color: #d43f3a;
}
